import React, { useState, useEffect, useContext } from 'react'
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import useStyles , {sxStyle} from '../../components/style/pages/mainImageStyles'
import { WhiteDiscoverButton } from '../../components/style/pages/eventsSection'
import { GatsbyImage } from "gatsby-plugin-image"
import { navigate } from 'gatsby-link'
import { AuthContext } from '../../context/general'


function MainImage({ data }) {

    const texts = ['Apprendre et comprendre ensemble', 'Nous connecter aux savoirs', 'Découvrir à tout âge', 'Cultiver notre curiosité', 'Nous ouvrir au monde', 'Découvrir à tout âge']
    const [currentWord, setCurrentWord] = useState(0)
    const [currentLetter, setCurrentLetter] = useState(0)
    const [flag, setFlag] = useState(false)

    const { authState, authActions } = useContext(AuthContext)





    useEffect(() => {

        const interval = setInterval(() => {

            if (currentLetter === texts[currentWord].length - 1)
                setFlag(true)

            if (currentLetter === texts[currentWord].length) {
                setCurrentLetter(0)
                setCurrentWord((currentWord + 1) % texts.length)
                setFlag(false)


            }
            else {
                setCurrentLetter(currentLetter + 1)
                setFlag(false)

            }
        }, !flag ? 100 + Math.random() * 75 : 3000);
        return () => clearInterval(interval)


    }, [currentLetter])

    if (!data)
        return null

    const gatsbyImage = data.pageImage ? data.pageImage.gatsbyImageData : data.mainImage.gatsbyImageData


    return (
        <Box height="50vh">


            <Box sx={sxStyle.mainImageContainer}>
                <GatsbyImage
                    image={authState.slug || gatsbyImage}
                    alt={data.pageMarkdown?.frontmatter?.title  ? data.pageMarkdown.frontmatter.title : 'pekoia homepage image'}
                    style={sxStyle.mainImage}
                />
                <Box position="relative" bottom='50vh' height='50vh'

                    style={{
                        background: `linear-gradient(
                    -90deg,
                    rgba(150, 113, 50, 0.25),
                    rgba(150, 113, 50, 0.25) 60%,
                    rgba(150, 113, 50, 0.25) 
                    
                  )`,
                        backgroundSize: 'cover'
                    }}>


                </Box>
            </Box>
            <Box sx={sxStyle.mainContainer}>
                <Box flex={2} >
                    <Box sx={sxStyle.paddingContainer}>



                        <Box >
                            <Typography style={{ color: '#FFF5F5', fontSize: '2.8rem', fontWeight: 200 }}>
                                PEKOIA
                            </Typography>
                        </Box>
                        <Box position="relative" bottom="2vh" display="flex" flexDirection="row" width="90%" flexWrap="nowrap">

                            <Box height="5vh">
                                <Typography sx={sxStyle.titleSecondaryText}>
                                    {texts[currentWord].slice(0, currentLetter)}
                                </Typography>


                            </Box>



                        </Box>
                    </Box>
                </Box>


                <Box flex={1} display="flex" flexDirection="row" alignContent="center" alignItems="center">

                    <Box>
                        <WhiteDiscoverButton onClick={()=>{
                            navigate('/about')
                        }}>
                            Découvrir
                        </WhiteDiscoverButton>

                    </Box>


                </Box>

            </Box>


        </Box>

    );
}

export default MainImage

