import { useContext } from 'react'

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'

import React from 'react'
import { AuthContext } from '../../context/general'

import { filtresSourceIndexed } from '../../helpers/filters'
import { navigate } from 'gatsby'



import { SwiperSlide } from 'swiper/react'




import { useStyles, sxStyle } from '../../components/style/pages/mainFilterStyles'





const VignetteFiltre = ({ data: elem, carouselHeight, index, sideData }) => {

    const classes = useStyles()

    const { authActions } = useContext(AuthContext)

    const handleLinkClick = (e, target) => {




        const categoriesData = sideData.markdowns.nodes.map(elem => elem.frontmatter)
        const position = categoriesData.find(elem => elem.position === index).key
        const slug = categoriesData.find(elem => elem.position === index).slug
        const gatsbyImagesData = sideData.pageImages.edges.map(elem => elem.node.gatsbyImageData) //.find(elem => elem.images.fallback.includes(slug))
        const gatsbyImageData = gatsbyImagesData.find(elem => elem.images.fallback.src.replace('%C3%A9', 'é').includes(slug))


        authActions.resetTopic({




            index: filtresSourceIndexed[position],                    //speciial code for reset to null
            filterItem: 'catégorie'
        })

        navigate(`/${slug}/`)

        //authActions.setSlug(gatsbyImageData)

        console.log('@--DEBUG')
        console.log(categoriesData.find(elem => elem.position === index))


    }

    return (<SwiperSlide><Box position="relative" width='100% ' height={carouselHeight + 'px'}
    >
        <AuthContext.Consumer>
            {context => (

                <Box width="100%">
                    <Box borderTop={`6px solid ${elem.color}`} width="100%">

                        <Box marginTop="3px" width="100%" position="relative">
                            <Box
                                onClick={e => handleLinkClick(e, '#experience')}
                                style={{
                                    width: '140px',
                                    color: 'black',
                                    height: 60,
                                    textDecoration: `none`,
                                    cursor: 'pointer'

                                }}
                            >
                                <Typography sx={sxStyle.filterLabel} style={{ color: 'black' }}>
                                    {elem.label}
                                </Typography>
                            </Box>


                        </Box>



                    </Box>



                </Box>)}
        </AuthContext.Consumer>
    </Box></SwiperSlide>)
}
export default VignetteFiltre




















