function MainFilter({ data, ssrTest }) {

    const classes = useStyles()
    const [position, setPosition] = useState(0)
    const matches = useMediaQuery(theme => theme.breakpoints.up('md'))
    const matchesXl = useMediaQuery(theme => theme.breakpoints.up('xl'))
    const [isActive, setIsActive] = React.useState(false)
     const [isMounted, setIsMounted] = React.useState(false)

    const FORMAT = 'FORMAT'
    const DATE = 'DATE'
    const CATEGORIE = 'CATEGORIE'
    const NIVEAU = 'NIVEAU'

    const initialState = {
        [FORMAT]: false,
        [DATE]: false,
        [CATEGORIE]: false,
        [NIVEAU]: false

    }


    // @TOTO CHANGE
    useEffect(()=>{
            if (!isMounted)
                setIsMounted(true)
    },[])

    //const isMounted = ssrTest

 

    if (!data)
        return null


  

    const searchTable = data.markdowns.nodes
        .filter(({ frontmatter }) => frontmatter.type === 'filtre')
        .map(({ frontmatter }) => ({ label: frontmatter.title, slug: frontmatter.slug, color: frontmatter.color, position: frontmatter.position }))
        .sort((a, b) => a.position - b.position)
        .map(elem=>({...elem, pageMarkdown: data.pageMarkdown}))
        .filter(elem => elem.position <=3)

     
  
    const filterButtonsFrameWorkSorted = filterButtonsFrameWork.sort((a, b) => a.index - b.index)


    // ADDED AS A QUICK FIX
    // NEED TO BE REMOVED FROM MODULE
    const sxLargeContainer = {
        display: {
            xs: 'block',
            sm: 'block',
            md: 'flex',
            lg: 'flex',
            xl: 'flex'
          },
          flexDirection: {
            xs: 'row',
            sm: 'row',
            md: 'column',
            lg: 'column',
            xl: 'column'
          },
          width : '100%'


    }
    // END COMMENT

    


    return (
        <Box sx={sxLargeContainer} >
              <Carousel noAvatar={true} ssrTest={ssrTest} isMounted={isMounted} cellTemplate={VignetteFiltre} cellsData={searchTable} carouselConfig={carouselConfig} isActive={isActive} setIsActive={setIsActive} sideData={data}/>
            
        </Box>

    );
}

export default MainFilter

const FilterButton = ({ label, index, state, filterItem }) => {

    const { authState } = React.useContext(AuthContext)


    const selected = authState?.filtres[filterItem]

    const [open, setOpen] = React.useState(false)
    const anchorRef = React.useRef(null)



    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };



    const style = { fontSize: '1rem' }

    return (<Box padding='5px'>
        <BottomFilterButton onClick={handleToggle} ref={anchorRef} selected={Boolean(selected)}>

            {label} {state ? <KeyboardArrowUpIcon style={style} /> : <KeyboardArrowDownIcon style={style} />}
        </BottomFilterButton>
        <FilterMenu anchorRef={anchorRef} filterItem={filterItem} open={open} setOpen={setOpen} index={index} />

    </Box>)
}

const FilterMenu = ({ anchorRef, setAnchorEl, open, setOpen, index, filterItem }) => {


    const filterButtons = filterButtonsFrameWork.find(button => button.position === index)

    const menuitems = filterButtons.choices

    const {  authActions } = React.useContext(AuthContext)


    const handleClick = (index) => (event) => {

        authActions.toggleTopic({
            index: index,
            filterItem: filterItem
        })
    }

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpen(false);
        }
    }

    const handleClose = () => {
        setOpen(false)
    }


    return (<Popper style={{ zIndex: 999 }} placement="right" open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
        {({ TransitionProps, placement }) => (
            <Grow
                {...TransitionProps}
                style={{ transformOrigin: 'center top' }}
            >
                <Paper>
                    <ClickAwayListener onClickAway={handleClose}>
                        <MenuList autoFocusItem={open} id={"menu-list-grow" + index} onKeyDown={handleListKeyDown}

                        >
                            {menuitems.map((label, index) => <LocalMenuItem filterItem={filterItem} key={index} index={index} onClick={handleClick(index)} label={label} />)}

                        </MenuList>
                    </ClickAwayListener>
                </Paper>
            </Grow>
        )}
    </Popper>)
}

const LocalMenuItem = ({ onClick, label, index, filterItem }) => {


    const { authState } = React.useContext(AuthContext)




    const show = authState.filtres[filterItem] & Math.pow(2, 15 - index)

    return (
        <MenuItem style={{ borderLeft: show ? '5px solid #50C0C0' : '5px solid transparent' }} onClick={onClick}  >{label}</MenuItem>
    )
}

