import WebinarIcon from '../../assets/webinar.svg'
import ConferenceIcon from '../../assets/conférence.svg'


import  {sxStyle} from '../../components/style/pages/eventsSection'


import { filterEventsOnCategory } from '../../helpers/filters'




function ConferencesSection({ data, isMounted }) {

    const { authState, authActions } = React.useContext(AuthContext)
    const [oddEvents, setOddEvents] = useState([])
    const [evenEvents, setEvenEvents] = useState([])
    const [cycles, setCycles] = useState([])
    const [events, setEvents] = useState([])
    const [mainImageSrc, setMainImageSrc] = useState(null)
    const [blurred, setBlurred] = useState(false)

    const portraits = data?.portraits?.edges.map(({ node }) => node)


    useEffect(() => {

        const key = data?.markdownRemark?.frontmatter.key
        if ((key === 0) || key) {
            authActions.resetTopic({
                index: filtresSourceIndexed[data.markdownRemark.frontmatter.key],                    //speciial code for reset to null
                filterItem: 'catégorie'
            })
        }

    }, [])






    useEffect(() => {

        let timer = null


        if (authState.slug) {
            if (authState.slug.images.fallback.src != blurred)
                setBlurred(authState.slug.images.fallback.src)
        }


        if (authState?.filtres) {


            const newCycles = data.cycles.edges.map(({ node }) => node)
            setCycles(newCycles)

            const startDate = authState.filtreDate?.startDate

            const catégorieSortKey = authState.filtres.catégorie ? authState.filtres.catégorie : (Math.pow(2, 16) - 1)
            const formatSortKey = authState.filtres.format ? authState.filtres.format : (Math.pow(2, 16) - 1)

            const now = DateTime.local()

          



            const newEvents = filterEventsOnCategory(data.events.edges
                .map(({ node }) => node)
                .filter(event => event.state == 'published')
                , catégorieSortKey, now, startDate)



            let newOddEvents = []
            let newEvenEvents = []

            if (newEvents.length > 8) {
                newOddEvents = newEvents.map((event, index) => (index % 2 === 1) ? event : null)
                    .filter(event => event)

                newEvenEvents = newEvents.map((event, index) => (index % 2 === 0) ? event : null)
                    .filter(event => event)

            }
            else if (newEvents.length > 4) {
                newEvenEvents = newEvents.slice(0, 4)
                newOddEvents = newEvents.slice(4, 8)

            }
            else
                newEvenEvents = newEvents


            setEvents(newEvents)
            setEvenEvents(newEvenEvents)
            setOddEvents(newOddEvents)

        }

        return () => {
            timer && clearTimeout(timer)
        }


    }, [authState])


    


    if (!data)
        return null

    const getFormatSortCode = (event) => {

        const format = event.format

        const index = eventFormat.indexOf(format)
        return Math.pow(2, 15 - index)

    }



    // REMOVED FOR NOW
    // const webinarEvents = events.filter(event => event.format === 'WEBINAR')
    // const atelierEvents = events.filter(event => event.format === 'ATELIER')



    return (
        <Box >
            <Box sx={sxStyle.sectionContainer} >
                <Box width="100%" paddingBottom="15px" >
                    <Carousel isMounted={isMounted} cellTemplate={VignetteEvent} cellsData={evenEvents} sideData={portraits} carouselConfig={carouselConfigEven} blurred={blurred}/>


                    <Carousel isMounted={isMounted} cellTemplate={VignetteEvent} cellsData={oddEvents} sideData={portraits} carouselConfig={carouselConfigOdd} blurred={blurred}/>

                </Box>
             

            </Box>
        </Box>

    );
}


