import React, { useState, useEffect } from 'react'
import Box from '@mui/material/Box'

import { StaticImage } from 'gatsby-plugin-image'
import useStyles from '../../components/style/pages/eventsSection'
import Logo from '../../assets/_logo_pekoia_vertical.svg'
import { testEmail } from '../../helpers/useful'

import CircularProgress from '@mui/material/CircularProgress'
import { handleNewMail } from '../../helpers/fetchers'
import { GatsbyImage } from "gatsby-plugin-image"









function SecondPhoto(props) {

    const { newsletterRef, data } = props

  
   
    const [showInput, setShowInput] = useState(false)
    const [textInput, setTextInput] = useState('')
    const [showSpinner, setShowSpinner] = useState(false)


    const handleClick = () => {
        setShowInput(!showInput)


    }






    const handleValidate = (event) => {

        // event.preventDefault()

        setShowSpinner(true)
        handleNewMail(textInput)

        if (typeof window !== "undefined") {
            if (window.fbq != null) {
                window.fbq('track', 'Lead');
            }
        }



        setTimeout(() => {
            setTextInput('')
            setShowInput(!showInput)
            setShowSpinner(false)
        }, 1000)

    }

    return (
        <div
        >





            <div style={{ backgroundColor: '#2f3542', marginTop: '50px', padding: '20px' }}>
                

                <div className="row flex-sm-row-reverse justify-content-center justify-content-sm-start" border="2px solid red">



                    <div className="col-lg-7 col-md-7 col-sm-8 " >


                        <h2 className="mb-50 small color-white" >
                            Tenez vous informé
                        </h2>
                        <div className="text-adaptive color-white px-20" >
                            Tenez-vous informé des prochaines conférences en ligne en vous abonnant à notre newsletter hebdomadaire
                        </div>
                        <div ref={newsletterRef} className="mt-20 d-flex flex-wrap justify-content-center justify-content-md-start" >
                            <input
                                type="email"
                                name="email"
                                value={textInput}
                                onChange={(e) => {
                                    setTextInput(e.target.value);
                                }}
                                placeholder="Votre email"
                                className="input mb-20 mr-10 w-370 lg border-gray focus-action-white color-white text-center text-md-left placeholder-white" />

                            <button className={testEmail(textInput) ? "btn lg action-1 w-370" : "btn lg action-white w-370 "} onClick={() => { testEmail(textInput) && handleValidate() }}>
                                Envoyer
                            </button>
                            <Box position="relative" right="100px" top="10px">
                                {showSpinner && <CircularProgress size={40} />}
                            </Box>

                        </div>

                    </div>

                </div>
            </div>
        </div>

    )




   
}

export default SecondPhoto

