import React, { useEffect, useState } from 'react'


import Box from '@mui/material/Box'
import useStyles from '../../components/style/pages/eventsSection'
import { AuthContext } from '../../context/general'



import Carousel from '../../components/caroussels/carousel'
import VignetteCycle from '../../components/caroussels/vignetteCycle'
import CycleIcon from '../../assets/cycle.svg'

import { grey } from '@mui/material/colors';


//import ReplaySections from './replaySections'




function CyclesSection({ data }) {

    const { authState } = React.useContext(AuthContext)
    const [cycles, setCycles] = useState([])
    const [isMounted, setIsMounted] = useState(false)

    const portraits = data?.portraits?.edges.map(({ node }) => node)

   

 


   
    useEffect(() => {

        const now = new Date()

        if (!isMounted)
        setIsMounted(true)

        if (authState?.filtres) {

            const newCyclesRaw = data.cycles.edges.map(({ node }) => node)
            const newCycles = newCyclesRaw.map(cycle => {

                

                const events = cycle.events
                .filter(event => event.title1)
                .filter(event => event.date >now.getTime()/1000)
                .sort((a,b) => a.date - b.date)

                if (events.length == 0)
                    return null

                const nextDate = events[0].date
              
             

                if (nextDate < now.getTime()/1000)
                    return null
            
                    return {...cycle, nextDate: nextDate}

            })

           
         
           

            setCycles(newCycles.filter(elem => elem).sort((a,b) => a.nextDate - b.nextDate))


        }


    }, [authState])

  


    if (!data)
        return null



    return (
        <div  style={{backgroundColor: 'black', marginTop: '50px', padding:"0px 20px 40px 20px"}}>
            
            <Carousel sideData={portraits} isMounted={isMounted}  cellTemplate={VignetteCycle} cellsData={cycles} carouselConfig={carouselConfigCycle} mode="dark" />
            </div>
    
    );
}


export default CyclesSection




