import React, { useEffect, useState } from 'react'

import { AuthContext } from '../../context/general'

import Carousel from '../../components/caroussels/carousel'
import VignetteReplay from '../../components/caroussels/vignetteReplay'
import CycleIcon from '../../assets/cycle.svg'
import { DateTime } from 'luxon'

import useStyles, { DiscoverButton } from '../../components/style/pages/eventsSection'
import { filterReplaysOnCategory } from '../../helpers/filters'

import { grey } from '@mui/material/colors';
import { Box } from '@mui/material/Box'
import { isMobile } from 'react-device-detect'

function ReplaySection({ data }) {

    const { authState } = React.useContext(AuthContext)
    const [replaysEven, setReplaysEven] = useState([])
    const [replaysOdd, setReplaysOdd] = useState([])
    const [isMounted, setIsMounted] = useState(false)

    const portraits = data?.portraits?.edges.map(({ node }) => node)

    const startDate = authState.filtreDate?.startDate

    const catégorieSortKey = authState.filtres.catégorie ? authState.filtres.catégorie : (Math.pow(2, 16) - 1)
    const formatSortKey = authState.filtres.format ? authState.filtres.format : (Math.pow(2, 16) - 1)

    const now = DateTime.local()

    useEffect(() => {

        if (!isMounted)
            setIsMounted(true)

        if (authState?.filtres) {

            const replaysRaw_ = data.replays.edges.map(({ node }) => node)

            const replaysRaw = filterReplaysOnCategory(replaysRaw_
                , catégorieSortKey, now, startDate)


            const replaysSorted = replaysRaw.filter(elem => elem).sort((a, b) => { return Math.random() - 0.5 })

            let newEvenEvens =[]
            let newOddEvents =[]

            if (replaysRaw.length > 4){
                
             newOddEvents = replaysSorted.map((event, index) => (index % 2 === 1) ? event : null).filter(event => event)
             newEvenEvens = replaysSorted.map((event, index) => (index % 2 === 0) ? event : null).filter(event => event)

             setReplaysEven(newEvenEvens)
             setReplaysOdd(newOddEvents)
            }

            else {
                setReplaysEven(replaysSorted)
            }


            
        }


    }, [authState])

    if (!data)
        return null

    return (
        <div style={{ backgroundColor: 'black', marginTop: '50px', padding: "0px 20px 40px 20px" }}>
            <Carousel sideData={portraits} isMounted={isMounted} cellTemplate={VignetteReplay} cellsData={replaysEven} carouselConfig={carouselConfigEvenReplay} mode="dark" />
            <Carousel sideData={portraits} isMounted={isMounted} cellTemplate={VignetteReplay} cellsData={replaysOdd} carouselConfig={carouselConfigOddReplay} mode="dark" />
        </div>

    );
}

export default ReplaySection

